/**
 * Seniority Classification.
 * @customfunction
 * @param {string} job_title_ .
 * @param {CustomFunctions.Invocation} invocation Invocation object.
 * @requiresAddress
 * @return status.
 */
// eslint-disable-next-line no-unused-vars
export async function seniority(job_title_: string, invocation): Promise<any> {
  const url = "https://api.thedatabusiness.net/api/coreapi/seniority-classification/";
  try {
    var api_key = await get_api_key();
    const axiosPromise = await axios({
      method: "POST",
      headers: {},
      auth: {
        username: "user",
        password: api_key,
      },
      data: {
        job_titles: [job_title_],
      },
      url: url,
    });

    var seniority = axiosPromise.data.result[0].seniority;
    var conf = axiosPromise.data.result[0].score;
    var data = [seniority, conf];
    var location = await get_current_location(invocation);
    write_data_next_columns(location[0], location[1], location[2], data);

    return data.length;
  } catch (error) {
    // console.log(error)
    return error.name + " , " + error.message + " , " + url + " ";
  }
}

/**
 * Job Function Classification.
 * @customfunction
 * @param {string} job_title_ .
 * @param {CustomFunctions.Invocation} invocation Invocation object.
 * @requiresAddress
 * @return status.
 */
// eslint-disable-next-line no-unused-vars
export async function job_function(job_title_: string, invocation): Promise<any> {
  const url = "https://api.thedatabusiness.net/api/coreapi/job-functions-classification/";
  try {
    var api_key = await get_api_key();
    const axiosPromise = await axios({
      method: "POST",
      headers: {},
      auth: {
        username: "user",
        password: api_key,
      },
      data: {
        job_titles: [job_title_],
      },
      url: url,
    });

    var jfs = axiosPromise.data.result[0];
    var data = [];
    for (let i = 0; i < jfs.length; i++) {
      data.push(jfs[i].job_function);
      data.push(jfs[i].score);
    }
    var location = await get_current_location(invocation);
    write_data_next_columns(location[0], location[1], location[2], data);

    return data.length;
  } catch (error) {
    // console.log(error)
    return error.name + " , " + error.message + " , " + url + " ";
  }
}

