// eslint-disable-next-line no-unused-vars, prettier/prettier
export async function organisation_data_db_by_name(name: string, invocation): Promise<any> {
  const url = "https://api.thedatabusiness.net/api/coreapi/organisation-data-db";
  try {
    var api_key = await get_api_key();
    const axiosPromise = await axios({
      method: "POST",
      headers: {},
      auth: {
        username: "user",
        password: api_key,
      },
      data: {
        search_by: "by_name",
        query: [name],
      },
      url: url,
    });

    var result = axiosPromise.data.result[0];
    if (result == null) {
      return "No Results";
    }

    var organization_name = result.organization_name;
    var website = result.website;
    var sector = result.sector.toString();
    var type = result.type.toString();
    var subtype = result.subtype.toString();
    var organization_tag = result.organization_tag.toString();
    var organization_number_uk = result.organization_number_uk;
    var legal_name = result.legal_name;
    var status = result.status;
    var linkedin = result.linkedin;
    var twitter = result.twitter;
    var facebook = result.facebook;
    var outlets_actual = result.outlets_actual;
    var has_outlets = result.has_outlets;
    var is_manufacturer = result.is_manufacturer;
    var is_brand_owner = result.is_brand_owner;
    var tier = result.tier;
    var turnover_actual = result.turnover_actual;
    var turnover_list = result.turnover_list.toString();
    var number_of_employees_actual = result.number_of_employees_actual;
    var number_of_employees_list = result.number_of_employees_list.toString();
    var parent_id = result.parent_id;
    var inheritance_type = result.inheritance_type;
    var organization_decription_list = result.organization_decription_list.toString();
    var organization_structure_list = result.organization_structure_list.toString();
    var organization_notes = result.organization_notes.toString();
    var extra = result.extra;
    var created_at = result.created_at;
    var last_edited = result.last_edited;
    var addresses = result.addresses.toString();
    var switchboard_numbers = result.switchboard_numbers.toString();
    var generic_emails = result.generic_emails.toString();
    var research_resources = result.research_resources.toString();


    var data = [
      organization_name,
      website,
      sector,
      type,
      subtype,
      sector,
      organization_tag,
      organization_number_uk,
      legal_name,
      status,
      linkedin,
      twitter,
      facebook,
      outlets_actual,
      has_outlets,
      is_manufacturer,
      is_brand_owner,
      tier,
      turnover_actual,
      turnover_list,
      number_of_employees_actual,
      number_of_employees_list,
      parent_id,
      inheritance_type,
      organization_decription_list,
      organization_structure_list,
      addresses,
      switchboard_numbers,
      generic_emails,
      research_resources,
      organization_notes,
      extra,
      created_at,
      last_edited,
    ];
    var location = await get_current_location(invocation);
    write_data_next_columns(location[0], location[1], location[2], data);

    return (
      data.length +
      "organization_name,website,sector,type,subtype,sector,organization_tag,organization_number_uk,legal_name,status,linkedin,twitter,facebook,outlets_actual,has_outlets,is_manufacturer,is_brand_owner,tier,turnover_actual,turnover_list,number_of_employees_actual,number_of_employees_list,parent_id,inheritance_type,organization_decription_list,organization_structure_list,addresses,switchboard_numbers,generic_emails,research_resources,organization_notes,extra,created_at,last_edited"
    );
  } catch (error) {
    // console.log(error)
    return error.name + " , " + error.message + " , " + url + " ";
  }
}

